import config from '@/config/config';
export default {
	data() {
		return {
			indicatorDots: true,
			interval: 3000,
			autoplay: true,
			swiper_class: {
				height: '400upx'
			},
			ImgList: [],
			mainItem: [],
			isCard: -1 //0、未权限；1、有权限
		};
	},
	onShow() {
		this.isCard = -1;
	},
	onLoad() {
		this.getList();
		this.getBanner();
		this.getUserUp();
	},
	methods: {
		// getDesc(count_down){
			// if(count_down == -1) {
			// 	return '已经开始'
			// }else if(count_down == -2){
			// 	return '已经结束'
			// }else {
			// 	return '待开启' + this.countdown(count_down)
			// }
		// },
		
		countdown(msec) {
			let _this = this;
			// // 目标日期时间戳
			// const end = Date.parse(new Date('2017-12-01'))
			// // 当前时间戳
			// const now = Date.parse(new Date())
			// // 相差的毫秒数
			// msec = end - now
			
			// 计算时分秒数
			let day = parseInt(msec  / 60 / 60 / 24)
			let hr = parseInt(msec  / 60 / 60 % 24)
			let min = parseInt(msec  / 60 % 60)
			let sec = parseInt(msec  % 60)
			// 个位数前补零
			hr = hr > 9 ? hr : '0' + hr
			min = min > 9 ? min : '0' + min
			sec = sec > 9 ? sec : '0' + sec
			// 控制台打印
			let str = `${day}天 ${hr}小时 ${min}分钟 ${sec}秒`;
			// console.log(str)
			// 一秒后递归
			
			
			if(msec == -1) {
				return '已经开始'
			}else if(msec == -2){
				return '已经结束'
			}else {
				// return '待开启' + str;
				console.log(str)
				return setTimeout(function() {
					msec --;
					_this.countdown(msec);
					return '待开启' + str;
				}, 1000)
				
			}
		},
		loadShop(item) {
			var _this = this;
			// if (item.status != 1) {
			// 	uni.showToast({
			// 		icon: 'none',
			// 		title: '此竞拍尚未开始'
			// 	});
			// 	return false;
			// }

			if (item.id == 1 || item.id == 4) {
				uni.navigateTo({
					url: '/pages/product/list?type=' + item.id
				});
			} else {
				if (_this.isCard == -1) {
					_this.$request.card
						.gradeList({})
						.then(data => {
							uni.hideLoading();
							if (data.status == 1) {
								var end_time = data.data.end_time; //结束时间
								if (end_time == 0) {
									_this.isCard = 0;
								} else {
									if (end_time * 1000 > Date.parse(new Date())) {
										_this.isCard = 1;
									} else {
										_this.isCard = 0;
									}
								}
							}
							_this.shopDump(item.id);
						});
				} else {
					_this.shopDump(item.id);
				}
			}
		},
		shopDump(id) {
			if (this.isCard == 1) {
				uni.navigateTo({
					url: '/pages/product/list?type=' + id
				});
			} else if (this.isCard == 0) {

				uni.showModal({
					title: '提示',
					content: '您尚未购买入场券，或已过期，是否前往购买？',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/card/list'
							});
						}
					},
				})
			}
		},
		getList() {
			//获取数据
			var _this = this;
			_this.$request.index
				.classify({})
				.then(data => {
					if (data.status == 1) {
						_this.mainItem = data.data;
						var h = new Date().getHours();
						_this.mainItem.forEach(item => {
							item.status = 0;
							var a = parseFloat(item.start_time / 3600);
							var b = parseFloat(item.end_time / 3600);
							item.start_hours = _this.getDate(a);
							item.end_hours = _this.getDate(b);
							if (a <= h && h <= b) {
								item.status = 1;
							}
						});
					}
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		getDate(hours) {
			//处理时间
			var h = Math.floor(hours);
			var m = hours % 1;
			var sh = h; //显示小时
			if (h < 10) sh = '0' + h;
			if (m > 0) m = Math.floor(60 * m);
			var sm = m; //显示分钟
			if (m < 10) sm = '0' + m;
			return sh + ':' + sm;
		},
		getBanner() {
			//获取广告
			var _this = this;
			_this.$request.index
				.carousel({})
				.then(data => {
					if (data.status == 1) {
						_this.ImgList = data.data;
					}
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		getUserUp() {
			//获取是否可以升级
			var _this = this;
			_this.$request.user
				.getUserData({})
				.then(data => {
					if (data.status == 1) {
						if (data.data.up == 1) {
							uni.setTabBarBadge({
								index: 3,
								text: '1'
							})
						} else {
							uni.setTabBarBadge({
								index: 3
							})
						}
					}
				});
		}
	}
};
